.navigation__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: rgba(0, 0, 0, 0.3);
}

.navigation__burger {
  padding: 0;
  margin: 0 25px 95px 365px;
  width: 40px;
}

.navigation {
  position: fixed;
  height: 100vh;
  width: 340px;
  top: 0;
  right: 0;
  color: black;
  background-color: white;
  padding: 15px 90px 90px 90px;
  z-index: 2;
}
